import React, { Component } from "react";
import AuthService from "../services/auth.service";
import { Redirect } from 'react-router-dom';
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import Header from './Header';
import Menu from './Menu';
import Footer from './Footer';
import axios from 'axios'




const required = value => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        This field is required!
      </div>
    );
  }
};

export default class CompleteSurvey extends Component {

  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.onChangeTitle = this.onChangeTitle.bind(this);
    this.onChangeSurveyCategory = this.onChangeSurveyCategory.bind(this);
    this.onChangeStatus = this.onChangeStatus.bind(this);
    this.onChangeOrigin = this.onChangeOrigin.bind(this);

    this.state = {
      title: "",
      category: "",
      status: "",
      origin: "",
      loading: false,
      message: "",
      survey: ""
    };
  }

  onChangeTitle(e) {
    this.setState({
      title: e.target.value
    });
  }

  onChangeSurveyCategory(e) {
    this.setState({
      category: e.target.value
    });
  }
  onChangeStatus(e) {
    this.setState({
      status: e.target.value
    });
  }

  onChangeOrigin(e) {
    this.setState({
      origin: e.target.value
    });
  }


  handleSubmit(e) {
    e.preventDefault();

    this.setState({
      message: "",
      successful: false,
      loading: true,
      meloading: true

    });

    const mytoken = JSON.parse(localStorage.getItem('user'));
    const token = mytoken.token;

    const responses = JSON.parse(localStorage.getItem('SingleResponse'));
    const response_data = responses.data;
    const get_form = response_data.get_form;
    // console.log(response_data);
    const value = this.state.title;


    // for(const form of get_form) {
    //   console.log(form.name)
    // }
    
    const body =
    {
      survey_response_pk: response_data.pk,
      form:

        [
           {
             get_form,
             value: value
           } 
          
        ]
      

    }

    console.log(body);

    axios.post("https://gfso.chinikiguard.com/survey/api/add/response/entry/", body,
      {
        headers:
        {
          'Content-Type': 'application/json',
          'Authorization': `Token ${token}`,
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
          'Access-Control-Allow-Credentials': true
        },
      })
      .then(res => {
        if (res.data) {
          localStorage.setItem("SuccessSurvey", JSON.stringify(res.data));
        }

        this.setState({
          message: res.data.detail,
          successful: true,
          loading: false,
          meloading: false
        });

        console.log(res);
        console.log(res.data.detail);
        // window.location = "/dashboard"
      },

        error => {
          const resMessage =
            (error.response &&
              error.response.data.detail &&
              error.response.data.detail) ||
            error.message ||
            error.toString();

          this.setState({
            successful: false,
            message: resMessage,
            loading: false,
            meloading: false
          });
        })
  }
  componentWillMount() {

  }

  render() {

    if (!localStorage.getItem('user')) {
      return (<Redirect to={'/'} />)
    }

    const SurveyResponse = JSON.parse(localStorage.getItem('SingleResponse'));
    const responseSurvey = SurveyResponse.data.get_form;

    const { loading } = this.state;



    return (
      <div>
        <Header />



        <div className="content-wrapper" style={{ minHeight: 1602 }}>
          <Menu />
          {/* Content Header (Page header) */}
          <section className="content-header">
            <div className="container-fluid">
              <div className="row mb-2">
                <div className="col-sm-6">
                  <h1>Survey</h1>
                </div>
                <div className="col-sm-6">
                  <ol className="breadcrumb float-sm-right">

                  </ol>
                </div>
              </div>
            </div>{/* /.container-fluid */}
          </section>
          {/* Main content */}
          <section className="content">
            <div className="container-fluid">
              <div className="row">
                <div className="col-md-3">
                </div>
                {/* /.col */}
                <div className="col-md-7">
                  <div className="card">
                    <div className="card-header p-2">
                      <ul className="nav nav-pills">
                        <li className="nav-item"><a className="nav-link " href="#activity" data-toggle="tab">Complete Survey</a></li>

                      </ul>
                    </div>{/* /.card-header */}
                    <div className="card-body">
                      <Form onSubmit={this.handleSubmit} ref={c => { this.form = c; }} className="form" id="">

                        {!this.state.messaga && (
                          <div>
                            {responseSurvey.map(response => (
                              <div>
                                <label>{response.name}</label>
                                <Input className="form-control" placeholder=""
                                  type={response.input_type}
                                  // value={this.state.title}
                                  onChange={this.onChangeTitle}
                                  validations={[required]}
                                />
                              </div>
                            ))}
                            <br />
                            <div className="form-group">
                              <button type="submit" className="btn btn-success btn-lg float-right"
                                disabled={this.state.loading}
                              >
                                {this.state.loading && (
                                  <span className="spinner-border spinner-border-sm"></span>
                                )}
                                <span>Complete <i className="fas fa-check" /></span>
                              </button>
                              <CheckButton
                                style={{ display: "none" }}
                                ref={c => {
                                  this.checkBtn = c;
                                }}
                              />
                            </div>
                            <center>
                              {this.state.message && (

                                <div className="pb-5" >
                                  <div
                                    className={
                                      this.state.successful
                                        ? "alert alert-custom alert-outline-success fade show mb-5"
                                        : "alert alert-custom alert-outline-danger fade show mb-5"
                                    }
                                    role="alert"
                                  >
                                    {this.state.message}
                                  </div>
                                </div>
                              )}</center>
                          </div>


                        )}
                      </Form>


                    </div>
                    {/* /.card-body */}

                  </div>
                  {/* /.card */}
                </div>
                {/* /.col */}
              </div>
              {/* /.row */}
            </div>{/* /.container-fluid */}
          </section>
          {/* /.content */}
        </div>

        <Footer />

      </div>



    );
  }
}



