import React, { Component } from "react";
import AuthService from "../services/auth.service";
import { Redirect } from 'react-router-dom';
import Form from "react-validation/build/form";
import Input from "react-validation/build/input";
import CheckButton from "react-validation/build/button";
import Header from './Header';
import Menu from './Menu';
import Footer from './Footer';
import axios from 'axios'




const required = value => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        This field is required!
      </div>
    );
  }
};

export default class AddSurvey extends Component {

  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.onChangeTitle = this.onChangeTitle.bind(this);
    this.onChangeSurveyCategory = this.onChangeSurveyCategory.bind(this);
    this.onChangeStatus = this.onChangeStatus.bind(this);
    this.onChangeOrigin = this.onChangeOrigin.bind(this);

    this.state = {
      title: "",
      category: "",
      status: "",
      origin: "",
      loading: false,
      message: "",
    };
  }

  onChangeTitle(e) {
    this.setState({
      title: e.target.value
    });
  }

  onChangeSurveyCategory(e) {
    this.setState({
      category: e.target.value
    });
  }
  onChangeStatus(e) {
    this.setState({
      status: e.target.value
    });
  }

  onChangeOrigin(e) {
    this.setState({
      origin: e.target.value
    });
  }

  handleSubmit(e) {
    e.preventDefault();

    this.setState({
      message: "",
      successful: false,
      loading: true,
      meloading: true

    });

    this.form.validateAll();

    if (this.checkBtn.context._errors.length === 0) {
      AuthService.CreateSurvey(
        this.state.title,
        this.state.category,
        this.state.status,
        this.state.origin, 
      ).then(
        response => {
          this.setState({
            message: response.data,
            successful: true,
            loading: false,
            meloading: false
          });
          window.location = "/formBuilder"
        },

        error => {
          const resMessage =
            (error.response &&
              error.response.data.detail &&
              error.response.data.detail) ||
            error.message ||
            error.toString();

          this.setState({
            successful: false,
            message: resMessage,
            loading: false,
            meloading: false
          });
        }
      );
    }
  }

  componentWillMount() {

   

    const username = 'admin'
    const password = 'Pass@1234'
    const token = Buffer.from(`${username}:${password}`, 'utf8').toString('base64')
    axios.get("https://ecological.chinikiguard.com/projects/api/list/?all_record=1",
      {
        headers:
        {
          'Authorization': `Basic ${token}`,
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET,POST,HEAD,OPTIONS',
          'Access-Control-Allow-Credentials': true
        },

      })
      .then(res => {
        if (res.data) {
          localStorage.setItem("AllProjectData", JSON.stringify(res.data));



        }
        // console.log(res);
        // console.log(res.data);
        // window.location = "/dashboard"
      })

  }


  render() {

    const projectId = JSON.parse(localStorage.getItem('AllProjectData'))

    const { loading } = this.state;

    if (!localStorage.getItem('user')) {
      return (<Redirect to={'/login'} />)
  }

    return (
      <div>
        <Header />

        <div className="content-wrapper" style={{ minHeight: '2120.88px' }}>
          <Menu />
          {/* Content Header (Page header) */}
          <section className="content-header">
            <div className="container-fluid">
              <div className="row mb-2">

              </div>
            </div>{/* /.container-fluid */}
          </section>
          {/* Main content */}
          <section className="content">
            <div className="container-fluid">
              <div className="row">
                <div className="col-md-12">


                  <div className="card card">
                    <div className="card-header">
                      <h1 className="card-title">SURVEY</h1>
                    </div>

                   
                  </div>

                </div>

              </div>
              {/* /.row */}
            </div>{/* /.container-fluid */}
          </section>
          {/* /.content */}

        </div>
        <Footer />

      </div>


    );
  }
}
